const Result = props => {
  return (
    <>
      <p>Date: {props.result.date}</p>
      <p>Home or Away: {props.result.homeMatch}</p>
      <p>Opponent: {props.result.opponent}</p>
      <p>
        Score: {props.result.ourScore} - {props.result.opponentScore}
      </p>
      <hr />
    </>
  );
};

export default Result;
